* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Roboto', sans-serif;
  }
  
  .app{
    background: linear-gradient(0.3turn, #a6973f, #e2db10, #e7f63c);
  }
  
  
  main {
    padding-top: 2rem;
    padding-bottom: 6rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 3px solid cadetblue;
    border-radius: 6%;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 3px solid cadetblue;
    border-radius: 6%;
  }
  
  label {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1.2rem;
  }
  
  label p{
    font-family: 'Bangers', cursive;
    font-size: 24px;
    font-weight: 200;
    margin-right: 1.5rem;
  
  }
  
  .amount{
    width: 16rem;
    height: 4rem;
    font-size: 30px;
    padding: 15px;
    appearance: none;
    background: none;
    border: none;
    outline: none;
    border-radius: 16px;
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
    color: #313131;
    transition: 0.4s ease; 
  }
  
  .amount_input{
    background-color: rgba(18, 193, 236, 0.5);
  }
  
  .amount_input:focus{
    background-color: rgba(255, 255, 255, 0.75);
  }
  
  .amount_output{
    background-color: rgba(219, 39, 189, 0.137);
  }
  
  
  .to {
    margin: 1rem;
    width: 17vh;
    height: 17vh;
    background-image: url('./assets/arrow_to_down.svg');
  }
  
  
